.receipt-div{
  margin-left: 30%;
  margin-right: 30%;
  margin-bottom: 5%;
}
#search{
  margin-top: 5%;
  padding-right: 25%;
  padding-left: 25%;
  text-align: center;
}
#print{
  border-radius: 30px;
}

#table{
  border: 1px solid black;
  text-align: center;
  width: 100%;
}

td{
  border-left: 1px solid black;
  border-bottom: 1px solid black;
  width: 10%;
}

th{
  border-left: 1px solid black;
  border-bottom: 1px solid black;
  width: 10%;
}


@media all and (max-width: 767px){
  /* for mobile phones */
  .receipt-div{
    margin: 10%;
  }
  img{
    width: 100%;
  }
  .hide{
    display: none;
  }
  .print{
    border-right: 1px solid black;
  }
}


@media all and (min-width: 768px){
  .form-div{
    margin-left: 30%;
    margin-right: 30%;
  }
}

